import React from "react";
import logo from "./media/logo.svg";
import home1 from "./media/phone.jpg";
import home2 from "./media/bridge.jpg";
import home3 from "./media/truck.jpg";
import home4 from "./media/produce.jpg";
import "./App.scss";
import ParticlesBg from "particles-bg";
import AnimateHeight from "react-animate-height";
import { Fade } from "react-reveal";
import MDReactComponent from "markdown-react-js";

const contentful = require("contentful");

class App extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      height: window.innerHeight,
      sizeLimit: 768,
      burgerHeight: 0,
      teamData: [],
      portfolioData: [],
    };
  }

  componentDidMount() {
    var client = contentful.createClient({
      // This is the space ID. A space is like a project folder in Contentful terms
      space: "kpndngk418ru",
      // This is the access token for this space. Normally you get both ID and the token in the Contentful web app
      accessToken: "6gfR7ml1YUc663Qzrg5JZ3CaWZnj7gEqN46t9VoNcQU",
    });

    client.getEntries({ content_type: "teamMember" }).then((response) => {
      this.setState({ teamData: response.items });
    });

    client.getEntries({ content_type: "portfolioCompany" }).then((response) => {
      this.setState({ portfolioData: response.items });
      console.log(this.state.portfolioData);
    });
  }

  componentWillUnmount() {}

  updateWindowDimensions() {
    console.log(window.innerWidth);
  }

  handleIterate = (Tag, props, children, level) => {
    if (level === 1) {
      props = {
        ...props,
        className: "playfair",
      };
    }

    if (Tag === "a") {
      props = {
        ...props,
        className: "playfair red-a",
        target: "_blank",
      };
    }

    return <Tag {...props}>{children}</Tag>;
  };

  burgerExpand = () => {
    const currentHeight = this.state.burgerHeight;

    this.setState({ burgerHeight: currentHeight === 0 ? "auto" : 0 });
  };

  render() {
    return (
      <div className="full-body">
        <div className="navbar">
          <div className="container">
            <div className="w-100 mobile-burger d-hide">
              <div className="row w-100">
                <div className="col">
                  <img className="navbar-logo" src={logo} />
                </div>
                <div className="col">
                  <button className="expandBurger" onClick={this.burgerExpand}>
                    X
                  </button>
                </div>
              </div>

              <AnimateHeight
                duration={500}
                height={this.state.burgerHeight}
                className="expansion d-flex w-100"
              >
                <a className="w-100">About</a>
                <a className="w-100 my-4">Team</a>
                <a className="w-100">Portfolio</a>
              </AnimateHeight>
            </div>
            <div className="row m-hide">
              <div className="col-auto">
                <img className="navbar-logo" src={logo} />
              </div>
              <div className="col d-flex align-items-center justify-content-end">
                <div className="navlink-container playfair">
                  <a>About</a>
                  <a className="mx-5">Team</a>
                  <a>Portfolio</a>
                </div>
              </div>
            </div>
          </div>
        </div>
        <section className="home d-flex">
          <div className="container">
            <Fade>
              <div className="head-text">
                <h1 className="tbird-font">T-BIRD</h1>
                <h2 className="tbird-font">CAPITAL</h2>
                <h2 className="playfair mt-5">
                  An <a className="red">early-stage</a> venture capital firm and
                  <a className="red"> partner</a> to mission-driven
                  entrepreneurs.
                </h2>
              </div>
            </Fade>
          </div>
          <div className="home-abs">
            <img className="" src={require("./media/halfred.png")} />
          </div>
        </section>
        <section className="about my-5 py-5">
          <div className="container">
            <div className="row">
              <div className="col">
                <img className="w-100" src={require("./media/pattern.svg")} />
              </div>
              <div className="col-md-auto w-75">
                <h2 className="playfair weight-600 red">About Us</h2>
                <p className="playfair">
                  T-Bird Capital invests in early stage, mission-driven founders
                  tackling problems that matter. Our investment decisions are
                  conscientious of the problems in the world around us, as well
                  as the context in which our founders are solving those
                  problems. An investment from T-Bird Capital is a partnership,
                  and we help our founders any way we can to turn their dreams
                  into reality.
                </p>
              </div>
            </div>
          </div>
        </section>
        <section className="team">
          <div className="container">
            {this.state.teamData.map((person, key) => (
              <div className="row">
                <div className="col-md ">
                  <div className="w-100">
                    <img
                      className="w-100"
                      src={person.fields.profilePic.fields.file.url}
                    />
                  </div>
                </div>
                <div className="w-75 col-md-auto">
                  <MDReactComponent
                    text={person.fields.bio}
                    onIterate={this.handleIterate}
                  />
                </div>
              </div>
            ))}
          </div>
        </section>
        <section className="portfolio">
          <div className="container">
            <div className="row">
              {this.state.portfolioData.map((company) => (
                <div className="col mx-3 p-2">
                  <div className="img-container w-100 aspect-1">
                    <img
                      className="w-100"
                      src={company.fields.companyLogo.fields.file.url}
                    />
                  </div>
                  <div className="text-center">
                    <p className="playfair">
                      {company.fields.companyDescription}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default App;
